.steps
	position: relative
	z-index: 1
	margin-bottom: 20px
	border: 2px solid $primary
	+clr
	height: 50px
	line-height: 46px
	color: $text
	font-size: 18px 
	+r(480)
		border: 0
		height: auto
		line-height: 1
	li
		position: relative
		float: left
		width: 25%
		padding: 0 13px 0 40px
		background: #f0f0f0 
		+r(1023)
			text-align: center
			font-size: 13px
			padding: 0 5px
		+r(768)
			width: auto
			padding: 0 20px 
			background: none 
		+r(480)
			display: inline-block
			float: none 
		&:before
			content: ""
			position: absolute
			right: -20px
			top: -2px
			+arr(20,50,$primary,r) 
			+r(768)
				display: none
		&:after
			content: ""
			position: absolute
			right: -17px
			top: 0
			+arr(17,46,#f0f0f0,r) 
			+r(768)
				display: none
		&:first-child
			z-index: 4
			padding-left: 13px
		&:nth-child(2)
			z-index: 3
		&:nth-child(3)
			z-index: 2 
		&:last-child
			&:after,
			&:before
				display: none
		&.is-last	
			background: #fff
			color: $btn  
			&:after
				+arr(17,46,#fff,r)
			> span
				i
					display: inline-block
		&.is-active
			background: $primary
			color: #fff 
			&:after
				+arr(17,46,$primary,r)
		> span
			i
				display: none
				font-size: 17px
				vertical-align: middle
				margin: -3px 5px 0 0
			+r(480)
				display: block
				padding: 10px 0 
.table-simple_cart
	margin: 40px 0 
	width: 100%
	border-bottom: 1px solid $primary 
	min-width: 900px
	table
		width: 100%
	th
		padding: 15px 
	td
		padding: 15px 
		&:first-child
			width: 45%
		&:last-child
			width: 5%
			text-align: center
	.item
		margin: 0
		&:hover
			box-shadow: none
		&__main,
		&__img
			vertical-align: middle
		&__name
			height: auto
		.item_list
			clear: both
			margin-top: 30px
			.item__img,
			.item__main
				+r(1023)
					display: block
					float: none
			.item__main
				+r(1023)
					width: 100%
			.item__img
				width: 50%
				margin: 0 auto  
			
	.price
		text-align: left
.table-simple_cart-box
	td
		vertical-align: top
	.item
		&__main,
		&__img
			vertical-align: top
			img
				vertical-align: top

.action-add
	margin: 30px 0 
	.btn
		margin-top: 23px
.table_order
	table-layout: fixed
	.table__cell	
		&:first-child
			width: 70%
		&:last-child
			width: auto 
	.item
		&__img
			width: 10%
		&__name
			height: auto
		&__link
			font-size: 15px
.total
	&__head
		padding: 20px 
		background: #f2f2f2 
		text-align: right
	&__list
		display: block
		text-align: left
		margin: 0
		li
			+clr
		div
			float: right
		+ .note-text
			margin-top: 20px
	&__footer
		padding: 20px 0
		.link
			display: inline-block
			margin-top: 56px
			+r(767)
				margin: 20px 0 
		.note-text
			margin-bottom: 20px
		img
			max-width: 100%
			display: block
@for $i from 1 through 4
	.icon-digit-#{$i}
		display: inline-block
		vertical-align: middle
		margin-right: 20px
		+s(digit-#{$i})
.order-row
	position: relative
	margin-bottom: 60px
	.h3
		margin-bottom: 20px
	&__img
		position: absolute
		right: 35px
		top: 0
		img
			display: block
			max-width: 100%
		&.is-left
			right: auto
			left: -45px
			top: 20px
.link-cancel
	color: $hot
	&:hover
		color: $hot 