.table-simple_cart { margin: 40px 0; width: 100%; border-bottom: 1px solid #a7c66d; min-width: 900px; }
.table-simple_cart table { width: 100%; }
.table-simple_cart th { padding: 15px; }
.table-simple_cart td { padding: 15px; }
.table-simple_cart td:first-child { width: 45%; }
.table-simple_cart td:last-child { width: 5%; text-align: center; }
.table-simple_cart .item { margin: 0; }
.table-simple_cart .item:hover { box-shadow: none; }
.table-simple_cart .item__main, .table-simple_cart .item__img { vertical-align: middle; }
.table-simple_cart .item__name { height: auto; }
.table-simple_cart .item .item_list { clear: both; margin-top: 30px; }
@media only screen and (max-width: 1023px) { .table-simple_cart .item .item_list .item__img, .table-simple_cart .item .item_list .item__main { display: block; float: none; } }
@media only screen and (max-width: 1023px) { .table-simple_cart .item .item_list .item__main { width: 100%; } }
.table-simple_cart .item .item_list .item__img { width: 50%; margin: 0 auto; }
.table-simple_cart .price { text-align: left; }

.table-simple_cart-box td { vertical-align: top; }
.table-simple_cart-box .item__main, .table-simple_cart-box .item__img { vertical-align: top; }
.table-simple_cart-box .item__main img, .table-simple_cart-box .item__img img { vertical-align: top; }

.action-add { margin: 30px 0; }
.action-add .btn { margin-top: 23px; }

.table_order { table-layout: fixed; }
.table_order .table__cell:first-child { width: 70%; }
.table_order .table__cell:last-child { width: auto; }
.table_order .item__img { width: 10%; }
.table_order .item__name { height: auto; }
.table_order .item__link { font-size: 15px; }

.total__head { padding: 20px; background: #f2f2f2; text-align: right; }
.total__list { display: block; text-align: left; margin: 0; }
.total__list li { *zoom: 1; }
.total__list li:after { content: " "; display: table; clear: both; }
.total__list div { float: right; }
.total__list + .note-text { margin-top: 20px; }
.total__footer { padding: 20px 0; }
.total__footer .link { display: inline-block; margin-top: 56px; }
@media only screen and (max-width: 767px) { .total__footer .link { margin: 20px 0; } }
.total__footer .note-text { margin-bottom: 20px; }
.total__footer img { max-width: 100%; display: block; }

.icon-digit-1 { display: inline-block; vertical-align: middle; margin-right: 20px; background-image: url("../img/icons.png"); width: 42px; height: 40px; background-position: -46px -48px; }

.icon-digit-2 { display: inline-block; vertical-align: middle; margin-right: 20px; background-image: url("../img/icons.png"); width: 44px; height: 41px; background-position: -51px 0px; }

.icon-digit-3 { display: inline-block; vertical-align: middle; margin-right: 20px; background-image: url("../img/icons.png"); width: 47px; height: 44px; background-position: 0px 0px; }

.icon-digit-4 { display: inline-block; vertical-align: middle; margin-right: 20px; background-image: url("../img/icons.png"); width: 42px; height: 42px; background-position: 0px -48px; }

.order-row { position: relative; margin-bottom: 60px; }
.order-row .h3 { margin-bottom: 20px; }
.order-row__img { position: absolute; right: 35px; top: 0; }
.order-row__img img { display: block; max-width: 100%; }
.order-row__img.is-left { right: auto; left: -45px; top: 20px; }

.link-cancel { color: #ea4924; }
.link-cancel:hover { color: #ea4924; }

.menu-fixed { position: fixed; z-index: 10; top: 0; right: 0; bottom: 0; left: 0; transition: transform 0.3s; transform: translateX(100%); }
.menu-fixed.is-active { transform: translateX(0); }
.menu-fixed__in { position: absolute; z-index: 2; top: 0; bottom: 0; right: 0; padding: 65px 10px 30px 10px; width: 355px; background: #799a2a; overflow-y: auto; }
@media only screen and (max-width: 767px) { .menu-fixed__in { width: 100%; } }
.menu-fixed__overlay { position: absolute; z-index: 1; top: 0; bottom: 0; right: 0; left: 0; }
.menu-fixed .btn-menu { top: 75px; right: 20px; color: #fff; }
.menu-fixed .nav { padding: 0 30px; font-weight: 400; }
.menu-fixed .nav a { color: #fff; padding: 5px 10px; }
.menu-fixed .nav a:hover { background: #698625; }
.menu-fixed .form-action { margin: 20px 0 0 40px; }
.menu-fixed .form-action__btn { background: #a7c66d; color: #fff; }
.menu-fixed .form-action__btn:hover { background: #91b848; }
.menu-fixed .menu { margin: 20px 40px 20px -10px; }
.menu-fixed .menu__link { color: #fff; padding: 7px 0; clear: both; }
.menu-fixed .menu__img { float: left; width: 50px; text-align: center; }
.menu-fixed .menu__img span { font-size: 27px; color: #fff; }
.menu-fixed .menu__img span:before { margin: 0; }
.menu-fixed .menu__main { overflow: hidden; text-align: left; }


